
// components
.btn{
  display: inline-block;
  text-align: center;
  vertical-align: middle;
}

.card{
  display: flex;
  flex-direction: column;
  height: 100%;
  border-radius: 4px;
  border: 1px solid $gray-2;
  box-shadow: 0 2px 6px $gray-8;
  &-img{
    border-radius: 4px 4px 0 0;
  }
  &-body{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-grow: 1;
  }
}

// container
.container {
  max-width: 1320px;
  padding-left: 12px;
  padding-right: 12px;
  margin: 0 auto;
  @include bp-pc-large {
    max-width: 1140px;
  }
  @include bp-pc {
    max-width: 960px;
  }
  @include bp-tablet {
    max-width: 720px;
  }
  @include bp-mobile {
    max-width: 540px;
  }
}

$cols: 12;
@for $i from 1 through $cols{
  .col-#{$i}{
    width: 100% / 12 * $i;
    padding-left: 12px;
    padding-right: 12px;
  }
  @include bp-tablet{
    .col-md-#{$i}{
      width: 100% / 12 * $i;
      padding-left: 12px;
      padding-right: 12px;
    }
  }
}

.row{
  display: flex;
  flex-wrap: wrap;
  margin-left: -12px;
  margin-right: -12px;
  @include bp-mobile{
    & > *{
      width: 100%;
    }
  }
}

.offset-md-3{
  @include bp-tablet{
    margin-left: 25%;
  }
  @include bp-mobile{
    margin-left: 0;
  }
}

.no-gutter{
  margin: 0;
  & > *{
    padding: 0;
  }
}



// for color
@each $key, $value in $colors-map {
  .text-#{$key} {
    color: $value;
  }
  .bg-#{$key} {
    background-color: $value;
  }
  .btn-#{$key} {
    background-color: $value;
    color: $light;
  }
}

// for font
@each $key, $value in $font-size-map {
  .fz-#{$key} {
    font-size: $value;
  }
}

@include bp-tablet{
  @each $key, $value in $font-size-map {
    .fz-md-#{$key} {
      font-size: $value;
    }
  }
}

@include bp-mobile{
  @each $key, $value in $font-size-map {
    .fz-sm-#{$key} {
      font-size: $value;
    }
  }
}


@each $key,$value in $font-weight-map{
  .fw-#{$key}{
    font-weight: $value;
  }
}

.italic{
  font-style: italic;
}

