@mixin bp-pc-large {
  @media (max-width: 1399px) {
    @content;
  }
}

@mixin bp-pc {
  @media (max-width: 1199px) {
    @content;
  }
}

@mixin bp-tablet {
  @media (max-width: 991px) {
    @content;
  }
}

@mixin bp-mobile {
  @media (max-width: 767px) {
    @content;
  }
}

